
*{
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app {

  color: #2c3e50;
  margin-top: 60px;
}
#menu {
  width: 100px;

  padding-top: 15px;
  padding-right: 15px;
  border-right: 1px solid #2c3e50;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
#main {
  width: 75%;
  padding-right: 10px;
  float: right;
  padding: 15px;
  height: 100%;
top: 0;
}
#goPage{
  cursor: pointer;
  position: static;
  left:0;
  width: 113px;
  max-width: 150px;
  border: none;
  background: white;
  border-bottom: 1px solid #2c3e50 ;
  padding-top: 20%;
  color: #2c3e50;
  font-size: 15px;
}

#loadDataButton{
  cursor: pointer;
  border: none;
  background: white;
  border-bottom: 1px solid;
float: right;
margin-right: 50%;
color: #2c3e50;
font-size: 20px;
}
#saveButton{
  cursor: pointer;
  border: none;
  background: white;
  border-bottom: 1px solid;
margin-top: 5%;
margin-right: 40%;
color: #2c3e50;
font-size: 20px;
}
